.registerContainer {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: rgb(255, 255, 255);
}

.registerContainer .sec {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    font-family: 'Montserrat';
    justify-content: center;
    position: relative;
}

.registerContainer .sec:nth-child(1) {
    position: absolute;
    z-index: 4;
    background-color: white;
    width: 40%;
    height: 80%;
    border-radius: 20px;
}

.registerContainer .sec .innersection {
    width: 70%;
    height: max-content;
    display: flex;
    margin-left: 50px;
    margin-top: 40px;
    flex-direction: column;
}

.registerContainer .sec .innersection .head {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 35px;
    margin-bottom: 10px;
}

.registerContainer .sec .innersection .sub {
    font-weight: 400;
    font-size: 16px;
    color: rgb(46, 46, 46);
    width: 350px;
    line-height: 160%;
    margin-bottom: 10px;
}

.registerContainer .sec .innersection .inputContainer {
    width: 70%;
    margin-top: 25px;
    height: max-content;
}

.registerContainer .sec .innersection .btn {
    width: 70%;
    border-radius: 10px;
    margin-top: 45px;
    height: 50px;
}

.registerContainer .sec .innersection .link {
    width: 70%;
    height: max-content;
    text-align: center;
    margin-top: 40px;
    font-size: 13px;
    text-decoration: none;
    color: rgb(25, 0, 255);
}

.registerContainer .sec .innersection .inputContainer .label {
    font-weight: 600;
    font-size: 16px;
}

.registerContainer .sec .innersection .inputContainer .inputBox {
    width: 100%;
    height: max-content;
    position: relative;
}

.registerContainer .sec .innersection .inputContainer .inp,
.registerContainer .sec .innersection .inputContainer .inputBox .inp {
    width: 100%;
    border-radius: 10px;
    height: 50px;
    border: 2px solid rgb(221, 221, 221);
    outline: none;
    font-family: 'Montserrat';
    font-size: 14px;
    padding: 0 20px;
    margin-top: 10px;
}

.registerContainer .sec .innersection .inputContainer .inputBox .icon {
    position: absolute;
    top: 20px;
    right: 20px;
}

.registerContainer .sec .innersection .inputContainer .err {
    font-size: 12px;
    color: red;
    margin-top: 5px;
    padding: 0 5px;
}

.registerContainer .color {
    background: linear-gradient(45deg, #E975AB, #726CF8);
    position: relative;
    overflow: hidden;
    width: 100%;
}

.registerContainer .title {
    width: 41%;
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    text-align: right;
}

.registerContainer .sec .back {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: absolute;
    top: 30px;
    left: 25px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 25px rgba(180, 180, 180, 0.582);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.registerContainer .sec .msg {
    position: absolute;
    max-width: 200px;
    top: 30px;
    right: 30px;
    background-color: white;
    border-radius: 10px;
    padding: 16px 22px;
    font-size: 13px;
    box-shadow: 0 0 25px rgba(180, 180, 180, 0.384);
}

@media only screen and (max-width: 1178px) {
    .registerContainer .sec:nth-child(1) {
        width: 95%;
        height: 100%;
    }

    .registerContainer .sec .innersection .sub {
        width: 100%;
    }

    .registerContainer .sec .innersection {
        height: max-content;
        margin-left: 46px;
        width: 100%;
    }

    .registerContainer .sec .innersection .inputContainer {
        width: 80%;
    }
}